







































































































import NewRestaurantDialog from "@/components/dialogs/Meals.vue";
import { Meal, Restaurant } from "@/ts/models/models";
import { ApiHelper, MealHelper, StoreHelper } from "@/ts/services/api";

import { Component, Vue, Watch } from "vue-property-decorator";
import { Store } from "vuex";


@Component({
  components: {
    NewRestaurantDialog,
  },
})
export default class Stores extends Vue {
  search=''
  loading = false;
  deleteLoading = false;
  getError=false;
  deleteError = false;
  dialog = false;
  dialogDelete = false;
  staticUrl=ApiHelper.staticUrl

  headers: object[] = [
    {
      text: "Name",
      align: "start",
      value: "name",
    },

    {
      text: "Price",
      align: "start",
      value: "price",
    },
     {
      text: "Striked Price",
      align: "start",
      value: "strikedPrice",
    },

    { text: "Special", value: "special" },
    { text: "Spice", value: "spice" },
    { text: "Available", value: "available"},
    { text: "Action", value: "actions" },
  ];

  meals: Array<Meal> = [];
  editedIndex = -1;

  editedItem: Meal = {
    id:"",
    rid:"",
    name:"",
    available:false,
    spice:false,
    special:false,
    price:0.0,
    strikedPrice:0.0
  }

  defaultItem: Meal = {
    id:"",
    rid:"",
    name:"",
    available:false,
    spice:false,
    special:false,
    price:0.0,
    strikedPrice:0.0

  };

  created() {
    this.initialize();
  }

  get formTitle(): string {
    return this.editedIndex === -1 ? "New Item" : "Edit Item";
  }

  get title(): string {
    return `${this.$route.params.name} > Meals`
  }

  mapsUrl(latitude: number, longitude: number): string {
    return `https://www.google.com/maps/search/?api=1&query=@${latitude},${longitude}`;
  }

  async initialize() {
    this.loading = true;
    this.getError=false;
    try {
      console.log("____________________");
      this.meals = await MealHelper.getMeals(this.$route.params.rid);
    } catch (error) {
      console.log(error);
      this.meals = [];
      this.getError=true
    }
    this.editedItem = this.defaultItem;
    this.loading = false;
  }

  editItem(item: any) {
    console.log("hi");
    this.editedIndex = this.meals.indexOf(item);
    this.editedItem = Object.assign({}, item);
    this.dialog = true;
  }

  deleteItem(item: any) {
    this.deleteLoading = false;
    this.deleteError = false;
    this.editedIndex = this.meals.indexOf(item);
    this.editedItem = Object.assign({}, item);
    this.dialogDelete = true;
  }
  async changeStatus(id: string, val: boolean) {
    try {
      await StoreHelper.changeStoreStatus(id, val);
      this.initialize()
    } catch (error) {
      console.log(error);
    }
  }

  async deleteItemConfirm() {
    const item = this.meals[this.editedIndex];
    this.deleteLoading = true;

    console.log(item.id)

    try {
      await MealHelper.deleteMeal(item.id);
    } catch (error) {
      console.log(error);
    }
    this.closeDelete();
    this.initialize();
    this.deleteLoading = false;
  }

  close() {
    this.dialog = false;
    this.$nextTick(() => {
      this.editedItem = this.defaultItem;
      this.editedIndex = -1;
    });
  }

  closeDelete() {
    this.dialogDelete = false;
    this.$nextTick(() => {
      this.editedItem = Object.assign({}, this.defaultItem);
      this.editedIndex = -1;
    });
  }

  save() {
    if (this.editedIndex > -1) {
      Object.assign(this.meals[this.editedIndex], this.editedItem);
    } else {
      this.meals.push(this.editedItem);
    }
    this.close();
  }

  @Watch("dialog")
  ondialogDelete(val: any) {
    val || this.close();
  }

  @Watch("dialogDelete")
  ondialogClose(val: any) {
    val || this.closeDelete();
  }
}
